




















































import Vue from "vue";
import Component from "vue-class-component";
import Swipe from "../../components/Swipe.vue";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import comhead from "../../components/Comhead.vue";
import { Toast } from "vant";

@Component({
  components: {
    Swipe,
    comhead
  },
  mixins: [mixin]
})
export default class Cart extends Vue {
  @Action("setPrepareId") setPrepareId;

  @Action("setTabIndex") setTabIndex;
  result = [];
  checked = false;
  cartList = [];
  totalMoney = 0.00; //总金额
  maxHeightdiv(){
    return "height:" +(this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availHeight-152 )+"px;"
  }
 handleImageWidth1() {
    return (
      "width:" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth *
        0.45 +
      "px;height:" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth *
        0.45 +
      "px;"
    );
  }
  allSelect() {   
    if (this.checked) {
      let result = [];
      this.cartList.forEach((item, index) => {
        console.log();
        result.push(item.id);
      });
      this.result = result;
    }else{
      this.result=[];
    }
  }
  totalPrice() {
    var totalMoney = 0;
    for (var i = 0; i < this.cartList.length; i++) {
      if (this.result.indexOf(this.cartList[i].id)!== -1) {
        totalMoney = totalMoney+(this.cartList[i].price * this.cartList[i].num);
      } 
    }
    totalMoney=totalMoney*100;
    totalMoney.toString();
   return totalMoney;
  }
  checkchange(){
    if(this.result.length == this.cartList.length){
      this.checked = true;
    }else{
      this.checked = false;
    }
 
  }
  onSubmit() {
    if (this.result.length <= 0) {
      Toast('您还没选择商品');
      return;
    }

    Vue.prototype.$reqFormPost(
      "/prepare/order/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartIdList: this.result.join(",")
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        console.log(res);

        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        this.setPrepareId(res.data.data.prepareId);
        this.$router.push({
          path: "/settle"
        });
        console.log("预支付订单ID", res.data.prepareId);
      }
    );
  }
  deleteCart(index) {
    Vue.prototype.$reqFormPost(
      "/shop/cart/delete",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartIds: this.cartList[index].id
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        Toast("已删除");
        this.getCartList();
      }
    );
  }
  deleteshopCart() {
    if (this.result.length <= 0) {
      Toast('您还没选择商品');
      return;
    }
    Vue.prototype.$reqFormPost(
      "/shop/cart/delete",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartIds: this.result.join(",")
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        Toast("已删除");
        this.getCartList();
      }
    );
  }
  collect(index) {
    Vue.prototype.$reqFormPost(
      "/fav/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        goodsId: this.cartList[index].goodsId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        Toast("收藏成功");
        this.deleteCart(index);
      }
    );
  }
  getCartList() {
    Vue.prototype.$reqFormPost(
      "/shop/cart/query",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("查询购物车", res.data);
        this.cartList = res.data.data.carts;
      }
    );
  }
  pluscart(id,num){
    Vue.prototype.$reqFormPost(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId:id,  
        num:num,
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("加1", res.data);
        this.cartList = res.data.data.carts;
      }
    );
  }
  minuscart(id,num){
    Vue.prototype.$reqFormPost(
      "/shop/cart/updatenum",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        cartId:id,  
        num:num,
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("减1", res.data);
        this.cartList = res.data.data.carts;
      }
    );
  }

  goindex(){
    this.$router.push("/");
  }
  mounted() {
    this.setTabIndex(2);
     if(this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId != '' && this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token!= ''){
    this.getCartList();
        }
  }
}



